.split-container {
  background-color: rgb(222, 255, 255);
  padding: 30px;
  border-radius: 10px;
  align-self: flex-start;
}
.split-form {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: repeat(auto, 5);
  row-gap: 20px;
  column-gap: 10px;
}
.split-form button {
  grid-column: 2/3;
}
.split-form input,
select {
  padding: 5px 10px;
}
