body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 80%;
  margin: auto;
  margin-top: 100px;
  display: flex;
  gap: 50px;
  justify-content: center;
  transition: 1s;
}
.friends {
  width: fit-content;
  transition: 1s;
}
.friend {
  width: 340px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto;
  padding: 15px;
  border-radius: 10px;

  column-gap: 20px;
}
.name {
  font-weight: bold;
}
.money-state {
  grid-row: 2/3;
  grid-column: 2/3;
}
.profile {
  grid-area: 1/1/3/2;
  border-radius: 50%;
}
.btn {
  background-color: rgb(0, 224, 224);
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.2s;
}
.btn.select {
  grid-row: span 2; /* span 2 rows */
  align-self: center;
}
.btn:hover {
  background-color: aqua;
}

.friend.active {
  background-color: rgb(222, 255, 255);
}

.form {
  background-color: rgb(222, 255, 255);
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto auto;
  border-radius: 5px;
  padding: 10px;
  row-gap: 10px;
  margin-top: 20px;
}
.form input {
  padding: 5px 10px;
  outline: none;
}
.btn.add {
  grid-column: 2/3;
  border: none;
}
.btn.open,
.btn.close {
  margin-top: 20px;
  float: right;
}
